.footer {
  background-color: var(--primary-color);
  color: white;
  height: 58px;
  bottom: 0px;
  position: fixed;
  width: 100%;
  z-index: 200;
  flex-wrap: nowrap;
}

footer>.delimiterChild {
  align-items: center;
  display: flex;
  justify-content: space-between;
  padding-left: clamp(20px,3.9%,50px);
  padding-right: clamp(20px,3.9%,50px);
}

.footerTittle {
  font-size: var(--heading4);
  font-weight: var(--font-bold);
}

.iconsImg {
  margin: 0 15px;
}

@media all and (max-width: 415px) {
  /*Footer */
  .footer {
    z-index: 99;
  }

  .icons {
    display: none;
  }
}
