    --heading1: 24pt;
    --heading2: 20pt;
    --heading3: 16pt;
    --heading4: 14pt;
    --button1: 14pt;
    --button2: 16pt;
    --text1: 14pt;
    --text2: 12pt;
    --text3: 10pt; 
    --font-bold: 700;
    --font-medium: 500;
    --font-regular: 400;
}

